<template>
  <div style="padding-bottom:30px;display:flex;flex-direction:column;justify-content:center;align-items:center;">
    <el-card style="width:600px;margin-top:30px;">
        <h3>高德地图key</h3>
        <div style="padding-top:30px;display:flex;flex-direction:row;justify-content:space-between;margin-bottom:10px;" v-for="(v, k) in amapkeylist" :key="k">
            <div>
                <el-tag size="medium" type="info" style="margin-left:10px;">{{v}}</el-tag>
                <el-button type="primary" size="mini" style="margin:0 10px 0 10px" v-if="v==amapkey" disabled>当前选中</el-button>
                <el-button type="primary" size="mini" style="margin:0 10px 0 10px;" v-if="v!=amapkey" @click="chooseamapkey(v)">选择</el-button>
                <el-button type="text" size="mini" style="margin:0 10px 0 10px;" @click="removeamapkey(v)">移除</el-button>
            </div>
        </div>
        <div style="padding-top:30px;">
            <el-button type="primary" size="mini" style="margin:0 10px 0 10px" @click="dialogamapkeyVisible=true">添加</el-button>
        </div>
        <div style="margin-top:30px;display:flex;flex-direction:row;justify-content:center;">
            <el-link target="_blank" href="https://lbs.amap.com/api/javascript-api/guide/abc/prepare">参考 https://lbs.amap.com/api/javascript-api/guide/abc/prepare</el-link>
        </div>
    </el-card>

    <el-card style="width:600px;margin-top:30px;">
        <h3>接口域名</h3>
        <div style="padding-top:30px;display:flex;flex-direction:row;justify-content:space-between;margin-bottom:10px;" v-for="(v, k) in domainlist" :key="k">
            <div>
                <el-tag size="medium" type="info" style="margin-left:10px;">{{v}}</el-tag>
                <el-button type="primary" size="mini" style="margin:0 10px 0 10px" v-if="v==domain" disabled>当前选中</el-button>
                <el-button type="primary" size="mini" style="margin:0 10px 0 10px;" v-if="v!=domain" @click="choosedomain(v)">选择</el-button>
                <el-button type="text" size="mini" style="margin:0 10px 0 10px;" @click="removedomain(v)">移除</el-button>
            </div>
        </div>
        <div style="padding-top:30px;">
            <el-button type="primary" size="mini" style="margin:0 10px 0 10px" @click="dialogdomainVisible=true">添加</el-button>
        </div>
        <div style="margin-top:30px;display:flex;flex-direction:row;justify-content:center;">
            <el-link target="_blank" href="https://minapp.dev">参考 https://minapp.dev</el-link>
        </div>
    </el-card>

    <el-dialog title="添加高德地图key" :visible.sync="dialogamapkeyVisible">
        <el-form>
            <el-form-item label="高德地图key">
                <el-input v-model="amapkeyinput" autocomplete="off"></el-input>
            </el-form-item>
            <el-button @click="dialogamapkeyVisible = false">取消</el-button>
            <el-button type="primary" @click="addamapkey" :disabled="amapkeyinput == ''">添加</el-button>
        </el-form>
    </el-dialog>

    <el-dialog title="添加接口域名" :visible.sync="dialogdomainVisible">
        <el-form>
            <el-form-item label="接口域名">
                <el-input v-model="domaininput" autocomplete="off"></el-input>
            </el-form-item>
            <el-button @click="dialogdomainVisible = false">取消</el-button>
            <el-button type="primary" @click="adddomain" :disabled="domaininput == ''">添加</el-button>
        </el-form>
    </el-dialog>

    <el-button style="margin-top:30px;" type="primary" @click="go" :disabled="amapkey=='' || domain == ''">去控制台</el-button>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
        amapkeyinput: '',
        domaininput: '',
        amapkey: '',
        domain: '',
        amapkeylist: [],
        domainlist: [],
        dialogamapkeyVisible: false,
        dialogdomainVisible: false,
    };
  },
  created() {
      this.amapkey = localStorage.getItem('amapkey') || '';
      this.domain = localStorage.getItem('domain') || '';
      this.amapkeylist = JSON.parse(localStorage.getItem('amapkeylist')) || [];
      this.domainlist = JSON.parse(localStorage.getItem('domainlist')) || [];
      if(this.$route.query.amapjskey && this.$route.query.domain){
          this.amapkeyinput = this.$route.query.amapjskey;
          this.domaininput = this.$route.query.domain;
          this.addamapkey();
          this.adddomain();
          this.chooseamapkey(this.$route.query.amapjskey);
          this.choosedomain(this.$route.query.domain);
          this.go();
      }
  },
  methods: {
      addamapkey(){
          if(!this.amapkeylist.find(v=>v==this.amapkeyinput)){
              this.amapkeylist.push(this.amapkeyinput);
          }
          localStorage.setItem('amapkeylist', JSON.stringify(this.amapkeylist));
          this.dialogamapkeyVisible=false;
          if(this.amapkey == ''){
              this.chooseamapkey(this.amapkeyinput);
          }
          this.amapkeyinput='';
      },
      adddomain(){
          if(!this.domainlist.find(v=>v==this.domaininput)){
              this.domainlist.push(this.domaininput);
          }
          localStorage.setItem('domainlist', JSON.stringify(this.domainlist));
          this.dialogdomainVisible=false;
          if(this.amapkey == ''){
              this.choosedomain(this.domaininput);
          }
          this.domaininput='';
      },
      chooseamapkey(v){
          localStorage.setItem('amapkey', v);
          this.amapkey = v;
          localStorage.setItem('token', '');
          localStorage.setItem('notificationlist', '');
      },
      choosedomain(v){
          localStorage.setItem('domain', v);
          this.domain = v;
          localStorage.setItem('token', '');
          localStorage.setItem('notificationlist', '');
      },
      removeamapkey(v){
          var l = [];
          this.amapkeylist.forEach(vv=>{
              if(vv !== v){
                  l.push(vv);
              }
          });
          localStorage.setItem('amapkeylist', JSON.stringify(l));
          this.amapkeylist = l;
          if(this.amapkey == v){
              localStorage.setItem('amapkey', '');
              this.amapkey = '';
          }
      },
      removedomain(v){
          var l = [];
          this.domainlist.forEach(vv=>{
              if(vv !== v){
                  l.push(vv);
              }
          });
          localStorage.setItem('domainlist', JSON.stringify(l));
          this.domainlist = l;
          if(this.domain == v){
              localStorage.setItem('domain', '');
              this.domain = '';
          }
      },
      go(){
          location.href = '/';
      },
  }
};
</script>

